import React, { useState } from 'react'

import '../styles/Header.css'


// Import Icons
import searchIcon from '../imgSources/searchicon.svg'
import githubLogo from '../imgSources/githublogo.png'
// Import Icons

import GetPersons from './GetPersons'

const LandingPage = () => {
    const [isList, setIsList] = useState(true)
    
    const [searchCount, setSearchCount] = useState(parseInt(localStorage.getItem('numLastSearched'))) 

    // search input
    const [user, setUser] = useState('')
    const [lastSearches, setLastSearches] = useState({ // Optional (not used anywhere)
        firstSearch: null,
        secondSearch: null,
        thirdSearch: null
    }) // Optional (not used anywhere)
    // search input

    const searchFunction = e => {
        setUser(e.target.value)
        localStorage.setItem('searchInput', e.target.value)
    }

    const searchSubmitFunc = e => {
        if(e.target.className === 'search') {
            e.preventDefault()
        }

        localStorage.setItem('searchResult', user)

        if (searchCount === 0 || searchCount === 1 || isNaN(searchCount)) {
            setLastSearches({...lastSearches, firstSearch: user}) // Optional (not used anywhere)

            localStorage.setItem('firstSearch', user)

            localStorage.setItem('numLastSearched', '2')
            setSearchCount(parseInt(localStorage.getItem('numLastSearched')))

        }else if(searchCount === '2' || searchCount === 2) {
            setLastSearches({...lastSearches, secondSearch: user}) // Optional (not used anywhere)

            localStorage.setItem('secondSearch', user)

            localStorage.setItem('numLastSearched', '3')
            setSearchCount(parseInt(localStorage.getItem('numLastSearched')))

        }else if(searchCount === '3' || searchCount === 3){
            setLastSearches({...lastSearches, thirdSearch: user}) // Optional (not used anywhere)

            localStorage.setItem('thirdSearch', user)

            localStorage.setItem('numLastSearched', '1')
            setSearchCount(parseInt(localStorage.getItem('numLastSearched')))

        }

        setUser('')
    }

    return (
        <div>
            {/* {document.title = 'React App'} */}
            <div id="header">
                test
                <a href="/github-profiles/#/mojombo"><img alt="github logo" src={githubLogo} className="github-logo"/> </a>
                
                <span className="search"> {/* Search Input */}
                    <a href={`${localStorage.getItem('searchResult')}`}>
                        <img alt="search icon" onClick={searchSubmitFunc} src={searchIcon} className="search-icon"/>
                    </a>
 
                    {/* <a href={`${localStorage.getItem('searchResult')}`}> */}
                        {/* <form onSubmit={searchSubmitFunc}> */}
                        <input
                            onChange={searchFunction} 
                            value={user} 
                            type="text" 
                            placeholder="Search for user" 
                            className="search-box"
                        />
                    {/* </form> */}
                    {/* </a> */}
                    
                </span> {/* Search Input */}

                <span className="last-searches">
                    Latest Searches: <br />
                    {localStorage.getItem('firstSearch')} {' '}
                    {localStorage.getItem('secondSearch')} {' '}
                    {localStorage.getItem('thirdSearch')} 
                </span>
                
            </div>

            <GetPersons 
                lastSearches={lastSearches} 
                setIsList={setIsList} 
                isList={isList}
                user={user}
            />
        </div>
    )
}

export default LandingPage