import React, {useState, useEffect} from 'react'

import '../styles/person-gridStyle.css'
import '../styles/person-listStyle.css'

function GetRepos({ username, className }) {
    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const [items, setItems] = useState([]);
  
    // Note: the empty deps array [] means
    // this useEffect will run once
    // similar to componentDidMount()
    const getUsernameRepo = () => {
      fetch(`https://api.github.com/users/${username}/repos`)
        .then(res => res.json())
        .then(
          (result) => {
            setIsLoaded(true);
            setItems(result);
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            setIsLoaded(true);
            setError(error);
          }
        )
      }
    // eslint-disable-next-line
    useEffect(getUsernameRepo, []);

    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {

      return <div id="loading">Loading...</div>
    } else {
      if(Array.isArray(items) && items.length > 0){ // If Items (repos) Is Array then return repos
        return (
          <span className={`${className}`} > 
            <ul className={`${className}-repos`}>
              {/* Repos: */}
              {items.map(item => (
            <li key={item.id}>
              {item.name}
            </li> 
              )).slice(0, 3)} {/* Map Over User Repos Data And return First Three Repo */}
            </ul>
          </span>
        );
      }
      return (<><p>No repository found.</p> <br/> <br/></>)
    }
  }

  export default GetRepos