import React, {useState, useEffect} from 'react'
/* Import Components */
import GetRepos from './GetRepos.js'
/* Import Components */


import '../styles/person-gridStyle.css'
import '../styles/person-listStyle.css'
// Import Icons
import gridIcon from '../imgSources/gridicon.svg'
import listIcon from '../imgSources/listicon.svg'
// Import Icons

function GetPersons({isList, setIsList,
                     lastSearches, user}) {
    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const [items, setItems] = useState([]); 

    useEffect(() => {
      // setTimeout(() => {
        fetch(`https://api.github.com/users`)
        .then(res => res.json())
        .then(
          (result) => {
            setIsLoaded(true);
            setItems(result);
            localStorage.setItem('result', JSON.stringify(result))
          },

          (error) => {
            setIsLoaded(true)
            setError(error)
          }
        )
      // }, 1500)
    }, [])

    if (error) {
      return <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height:'80vh'}}>Error: {error.message}</div>;
    } else if (!isLoaded) {
      document.title = 'Loading...'
      return <div id="loading">Loading...</div>;
    } else {
      document.title = 'Github Profiles'
      if(Array.isArray(items)){
        let className = `${isList === true ? `person` : `person-grid`}` 
        return (
          <div className={`${className}-wrapper`}>
            <div className={`${className}-container`}>
              <h2 id={`${className}-list-of-persons`}>The Most popular users according to GitHub: </h2>
              <div className={className}>
                {/* List/Grid Switcher */}
                <span id="grid-list-grid-switcher">
                  {isList === true 
                ? <span onClick={() => setIsList(!isList)}><img alt="Grid Icon" src={gridIcon}/></span> 
                : <span onClick={() => setIsList(!isList)}><img alt="List Icon" src={listIcon} /></span>} 
                </span>
                {/* List/Grid Switcher */}
                  {items.map(item => (
                      <div className={`${className}-each-person`} key={item.id}>
                          <img alt="Avatar" href={`${item.login}`} className={`${className}-avatar`} src={item.avatar_url} />
                          <a className={`${className}-name`} href={`#/${item.login}`} onClick={() => {
                            localStorage.setItem('aseiyosaxla', JSON.stringify(item))
                            localStorage.setItem('person-index', JSON.stringify(items.indexOf(item)))
                          }}>{item.login}</a> 
                          <p className={`${className}-type`}>{item.type}</p> 
                          <GetRepos className={className} username={item.login}/>
                          <br /> <br />
                      </div>
                  ))} 
              </div>
            </div>
            <button style={{width:'100%', backgroundColor: '#2b3337', border: '0'}}></button>
          </div>
        );
      } 
        else { 
          return (
          <p className="no-info-avaiable">
            There Is Problem Retrieving Data From GitHub API... <br />
            Estimated Error: `Failed to load resource: 
            the server responded with a status of 403 (rate limit exceeded)`
           </p>)
        } 
    }
}

export default GetPersons