import React, { useEffect, useState } from 'react'

import GetRepos from './GetRepos'

import githubLogo from '../imgSources/githublogo.png'

import '../styles/SearchUserPage.css'
import '../styles/UsersUserPage.css'

import followerFollowing from '../imgSources/supervisor_account.svg'

export default function UserPage() {
    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const [item, setItem] = useState([]);

    const [orgs, setOrgs] = useState([]);

    const getUsernameRepo = () => {
        let searchResult = localStorage.getItem('searchResult')
        fetch(`https://api.github.com/users/${searchResult}`)
            .then(res => res.json())
            .then(
                (result) => {
                    setIsLoaded(true);
                    setItem(result);
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    setIsLoaded(true);
                    setError(error);
                }
            )
    }

    const getUserOrgs = () => {
        let searchResult = localStorage.getItem('searchResult')
        fetch(`https://api.github.com/users/${searchResult}/orgs`)
            .then(res => res.json())
            .then(
                (result) => {
                    setOrgs(result);
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    setError(error);
                }
            )
    }

    useEffect(() => {
        getUsernameRepo()
        return () => {
            // cleanup
        }
    }, [])

    useEffect(() => {
        getUserOrgs()
        return () => {
            // cleanup
        }
    }, [])

    let classname = 'search-user' 
    // eslint-disable-next-line
    {document.title = `${item.login} (${item.name})`}
    if(Array.isArray(orgs)) {
        return (
            <div className={`${classname}-page`}>
                {/* rate limit error handling */}
                {item.documentation_url === 'https://developer.github.com/v3/#rate-limiting' ? (
                    <p className="no-info-avaiable">
                        There Is Problem Retrieving Data From GitHub API...
                    <a href={`/github-profiles`}><button>Landing Page</button></a>
                    </p>) :
                    <div>
                        {/* back link to landing page */}
                        {/* <a href={`/`}><button>Landing Page</button></a> */}
                    


                        <div id={`${classname}-header`}>
                            <a href="/"><img alt="github-logo" src={githubLogo} className={`${classname}-logo`}/> </a>
                        </div>

                        <span>
                            {/* full name & username */}
                            <h1 id="full-name">{item.name}</h1>
                            <a style={{
                                textDecoration: 'none'
                            }} href={`https://github.com/${item.login}`}>
                                <h3 id={`${classname}-login`}>{`${item.login}`}</h3>
                            </a>

                            {/* type */}
                            <p style={{
                                marginTop: '5px',
                                opacity: '0.9'    
                            }}>{item.type}</p>

                            <br />
                            {/* avatar */}
                            <img alt="Avatar" src={item.avatar_url} id={`${classname}-img`}/>
                            
                            <br/>
                            {/* follower follwing */}
                            <p id={`${classname}-follower-following`}>
                                <img id="follower-following-img" src={followerFollowing} alt="supervisor_account" />
                                Followers: {item.followers} {' '} Following: {item.following}
                            </p>
                            
                            <br />
                            {/* repos */}
                            <GetRepos className={`${classname}`} username={localStorage.getItem('searchResult')} />

                            {/* organizations */}
                            <br />
                            {   Array.isArray(orgs) && orgs.length !== 0 
                            ?   <span id="oranization-selector">
                                    {
                                        orgs.map(item => {
                                            //eslint-disable-next-line
                                            return <div className={`organisation-img`}>
                                                        <img title={`${item.login}`} alt="organisation-img" className={`organisation-img`} src={item.avatar_url} />
                                                        <a style={{
                                                            textDecoration: 'none',
                                                            color: 'white'
                                                        }} href={`https://github.com/${item.login}`} target="_blank" rel="noreferrer">
                                                            <span id="organisation-login">{item.login}</span>
                                                        </a>
                                                        
                                                    </div>
                                        })
                                    }                         
                                    <br/>  
                                </span>
                            : <div>{item.name ? item.name.toLowerCase() : 'User'} is not belong to any organization</div>} 

                        </span>

                        {error ? <div>Error: {error.message}</div> : !isLoaded ? <div id="loading">Loading...</div> : null}
                    </div>}
                {/* rate limit error handling */}
            </div>
        )
    } // if Array.isArray(orgs) end
    else {
        return (
            <div className={`${classname}-page`}>
                {/* rate limit error handling */}
                {item.documentation_url === 'https://developer.github.com/v3/#rate-limiting' ? (
                    <p className="no-info-avaiable">
                        There Is Problem Retrieving Data From GitHub API...
                    <a href={`/`}><button>Landing Page</button></a>
                    </p>) :
                    <div>
                        {/* back link to landing page */}
                        {/* <a href={`/`}><button>Landing Page</button></a> */}
                    


                        <div id={`${classname}-header`}>
                            <a href="/"><img alt="github-logo" src={githubLogo} className={`${classname}-logo`}/> </a>
                        </div>
                        
                        {/* Not Found */}
                        <h1 style={{
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            height: '75vh'
                        }}>
                            **Not Found**
                        </h1>

                        {error ? <div>Error: {error.message}</div> : !isLoaded ? <div id="loading">Loading...</div> : null}
                    </div>}
                {/* rate limit error handling */}
            </div>
        )
    }
}
