import React from 'react'
import './App.css';

import { HashRouter, Route } from 'react-router-dom'

/* Import Components */
import LandingPage from './components/LandingPage'
import UsersUserPage from './components/UsersUserPage'
// eslint-disable-next-line
import SearchUserPage from './components/SearchUserPage'
/* Import Components */

function App() {
  let result = JSON.parse(localStorage.getItem('result'))
  let index = localStorage.getItem('person-index')

  let resultIndex;
  if(result === null || index === null || result.documentation_url === "https://developer.github.com/v3/#rate-limiting") {
      resultIndex = ''
      index = 0
  }else {
    resultIndex = result[index]
  }
  // let resultIndex = result[index] === null ? '/error' : result[index]

  // console.log(resultIndex.login === undefined)
  // console.log(localStorage.getItem('searchResult'), 'eg ari jigaro')

  return (
    <HashRouter>
        <Route exact path='/' component={LandingPage} />
        <Route exact path={`/${resultIndex.login === localStorage.getItem('searchResult') ? null : resultIndex.login}`} component={UsersUserPage}/>
        <Route exact path={`/${localStorage.getItem('searchResult')}`} component={resultIndex.login === localStorage.getItem('searchResult') ? UsersUserPage : SearchUserPage} />
    </HashRouter>
  );
}

export default App;
